import { useInView } from 'react-intersection-observer'

import { animate } from 'framer-motion'
import React, { useEffect, useRef, useState } from 'react'
import Typography from '@mui/material/Typography'
import Box from '@mui/material/Box'

import './style.css'
import { Avatar } from '@mui/material'
import { teal } from '@mui/material/colors'

function Counter ({ from, to, inView, useExp }) {
  const [hasLoaded, setHasLoaded] = useState(false)
  const nodeRef = useRef()
  useEffect(() => {
    const node = nodeRef.current
    if (inView && !hasLoaded) {
      setHasLoaded(true)
      animate(from, to, {
        duration: 1.5,
        onUpdate (value) {
          if (useExp && value >= 1e3) {
            node.textContent = ('' + value.toExponential(1))
              .replace('+', '')
              .toUpperCase()
          } else if (value >= 1) {
            node.textContent = value.toFixed(0)
          } else {
            node.textContent = value.toFixed(5)
          }
        },
        onComplete () {
          if (useExp) {
            node.textContent = ('' + to.toExponential(0))
              .replace('+', '')
              .toUpperCase()
          } else if (node.textContent >= 1) {
            node.textContent = to.toFixed(0)
          } else {
            node.textContent = to.toFixed(5)
          }
        }
      })
    }
  }, [from, to, inView])

  return <Typography variant='h4' className='stat-text' ref={nodeRef} />
}

export function Stat ({ stat, unit, children, useExp }) {
  const { ref, inView } = useInView()
  return (
    <Box className='stat-box'>
      <Avatar
        ref={ref}
        sx={{
          backgroundColor: teal[400],
          flexDirection: 'column',
          width: 150,
          height: 80,
          borderRadius: 2
        }}
      >
        <Counter from={0.1 * stat} to={stat} inView={inView} useExp={useExp} />
        {unit && (
          <Typography
            variant='h6'
            className='stat-text'
            sx={{ padding: 0, margin: 0, marginTop: -1 }}
          >
            {unit}
          </Typography>
        )}
      </Avatar>
      <Box className='stat-body'>{children}</Box>
    </Box>
  )
}

export function StatContainer ({ children }) {
  return <Box className='stat-container'>{children}</Box>
}
