import * as React from 'react'
import AppBar from '@mui/material/AppBar'
import Box from '@mui/material/Box'
import Toolbar from '@mui/material/Toolbar'
import IconButton from '@mui/material/IconButton'
import Typography from '@mui/material/Typography'
import MenuIcon from '@mui/icons-material/Menu'

import Container from '@mui/material/Container'
import Button from '@mui/material/Button'

import Drawer from '@mui/material/Drawer'
import List from '@mui/material/List'
import Divider from '@mui/material/Divider'
import ListItem from '@mui/material/ListItem'
import ListItemButton from '@mui/material/ListItemButton'

import { useSelector } from 'react-redux'

import Logo from '../logo'
import MUISwitch from '../muiSwitch'

import './styles.css'

import whitepaper from './Gradian_Whitepaper.pdf'

import LaunchIcon from '@mui/icons-material/Launch'

const pages = [
  { text: 'About', link: '#metaverse' },
  { text: 'Decentralised Apps', link: '#DAppEcosystem' },
  { text: 'Get Gradian', link: '#getGradian' },
  { text: 'Roadmap', link: '#roadmap' }
]

const ResponsiveAppBar = () => {
  const [menuOpen, setMenuOpen] = React.useState(false)

  const handleOpenNavMenu = () => {
    setMenuOpen(true)
  }

  const handleCloseNavMenu = () => {
    setMenuOpen(false)
  }

  const lightOn = useSelector((state) => state.light.value)
  const selectedTitle = useSelector((state) => state.title.value)

  const getBackground = (selectedTitle, link) => {
    if (lightOn) {
      return '#' + selectedTitle === link ? 'rgba(0,0,0,0.1)' : 'clear'
    }
    return '#' + selectedTitle === link ? 'rgba(255,255,255,0.25)' : 'clear'
  }

  return (
    <AppBar position='static' style={{ borderRadius: '5px' }}>
      <Drawer anchor='top' open={menuOpen} onClose={handleCloseNavMenu}>
        <Box
          sx={{ width: 'auto' }}
          role='presentation'
          onClick={handleCloseNavMenu}
          onKeyDown={handleCloseNavMenu}
        >
          <List>
            <ListItem>
              <Typography
                variant='h6'
                component='div'
                sx={{ flexGrow: 1, display: { xs: 'flex', lg: 'none' } }}
              >
                <Logo />
              </Typography>
              <Box
                sx={{
                  display: { xs: 'flex', lg: 'none' },
                  alignItems: 'center',
                  height: '100%'
                }}
              >
                <MUISwitch />
              </Box>
            </ListItem>
          </List>
          <Divider />
          <List>
            {[
              ...pages,
              {
                text: 'White Paper',
                link: whitepaper,
                extra: { target: '_blank', rel: 'noreferrer noopener' }
              }
            ].map(({ text, link, extra }, index) => (
              <ListItemButton
                sx={{
                  background: getBackground(selectedTitle, link),
                  padding: 1
                }}
                component='a'
                key={text}
                href={link}
                {...extra}
              >
                <Typography
                  sx={{ fontWeight: 600, fontSize: 18 }}
                  className='menu-font'
                >
                  {text}
                </Typography>
              </ListItemButton>
            ))}
            <ListItemButton
              sx={{
                padding: 1
              }}
              component='a'
              key='docs'
              href='https://github.com/gradian-network'
              target='_blank'
              rel='noreferrer noopener'
            >
              <Typography
                sx={{ fontWeight: 600, fontSize: 18 }}
                className='menu-font'
              >
                Docs
              </Typography>
              <LaunchIcon />
            </ListItemButton>
          </List>
        </Box>
      </Drawer>
      <Container maxWidth='xl'>
        <Toolbar disableGutters>
          <Box sx={{ display: { xs: 'none', lg: 'flex' } }}>
            <Logo />
          </Box>

          <Typography
            variant='h6'
            component='div'
            sx={{ display: { xs: 'flex', lg: 'none' } }}
          >
            <Logo />
          </Typography>
          <Box
            sx={{
              flexGrow: 1,
              display: { xs: 'flex', lg: 'none' },
              justifyContent: 'flex-end'
            }}
          >
            <IconButton
              size='large'
              aria-controls='menu-appbar'
              aria-haspopup='true'
              onClick={handleOpenNavMenu}
            >
              <MenuIcon sx={{ color: lightOn ? 'inherit' : 'white' }} />
            </IconButton>
          </Box>
          <Box sx={{ flexGrow: 1, display: { xs: 'none', lg: 'flex' } }}>
            {pages.map(({ text, link }) => (
              <Button
                key={text}
                href={link}
                onClick={handleCloseNavMenu}
                className='menu-text'
                sx={{
                  my: 2,
                  color: 'inherit',
                  display: 'flex',
                  background: getBackground(selectedTitle, link)
                }}
              >
                <Typography sx={{ fontWeight: 600 }} className='menu-font'>
                  {text}
                </Typography>
              </Button>
            ))}
            <Button
              key='docs'
              href='https://github.com/gradian-network'
              sx={{
                my: 2,
                color: 'inherit',
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center'
              }}
              className='menu-text'
              target='_blank'
              rel='noreferrer noopener'
            >
              <Typography sx={{ fontWeight: 600 }} className='menu-font'>
                Docs
              </Typography>
              <LaunchIcon />
            </Button>
          </Box>
          <Box sx={{ display: { xs: 'none', lg: 'flex' } }}>
            <MUISwitch />
          </Box>
          <Box sx={{ display: { xs: 'none', lg: 'flex' } }}>
            <span style={{ width: 50 }} />
            <Button
              variant='contained'
              color='secondary'
              size='large'
              style={{ borderRadius: 20 }}
              href={whitepaper}
              target='_blank'
              rel='noreferrer noopener'
            >
              White paper
            </Button>
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  )
}
export default ResponsiveAppBar
