import Typography from '@mui/material/Typography'
import Box from '@mui/material/Box'
import Avatar from '@mui/material/Avatar'

import './style.css'
import { deepOrange, deepPurple, green, red, blue } from '@mui/material/colors'

function randomColor () {
  const megaColor = [deepOrange, deepPurple, green, red, blue]
  const randIdx = Math.floor(Math.random() * megaColor.length)
  const colorRange = megaColor[randIdx]
  const randRange = Math.floor(Math.random() * 8) * 100 + 200
  return colorRange[randRange]
}

export function Feature ({ children }) {
  return <Box className='feature'>{children}</Box>
}

export function FeatureMedia ({ color, iconSrc }) {
  return (
    <Avatar
      sx={{
        width: 80,
        height: 80,
        bgcolor: randomColor(),
        margin: 2,
        padding: 1
      }}
    >
      <img src={iconSrc} className='icon' alt='feature-icon' />
    </Avatar>
  )
}

export function FeatureTitle ({ children }) {
  return (
    <Typography
      variant='h5'
      gutterBottom
      component='div'
    >
      {children}
    </Typography>
  )
}

export function FeatureBody ({ children }) {
  return (
    <Typography
      variant='subtitle1'
      className='feature-body'
      component='div'
      sx={{ fontSize: { xs: 22, md: 20 }, width: { xs: '100%', md: 350 } }}
    >
      {children}
    </Typography>
  )
}

export function FeatureContainer ({ children }) {
  return <Box className='feature-container'>{children}</Box>
}
