import IconButton from '@mui/material/IconButton'
import Box from '@mui/material/Box'
import React from 'react'
import Twitter from '@mui/icons-material/Twitter'
import Facebook from '@mui/icons-material/Facebook'
import Reddit from '@mui/icons-material/Reddit'
import { ReactComponent as Discord } from './discord.svg'
import { ReactComponent as DiscordWhite } from './DiscordWhite.svg'
import { useSelector } from 'react-redux'

export default function Social () {
  const lightOn = useSelector((state) => state.light.value)

  return (
    <Box sx={{ display: 'flex', padding: 2 }}>
      <IconButton
        aria-label='delete'
        size='large'
        color='inherit'
        href='https://twitter.com/GradianNetwork'
        target='_blank'
        rel='noreferrer noopener'
      >
        <Twitter />
      </IconButton>
      <IconButton
        aria-label='delete'
        size='large'
        color='inherit'
        href='https://www.facebook.com/Gradian-102619632430237'
        target='_blank'
        rel='noreferrer noopener'
      >
        <Facebook />
      </IconButton>
      <IconButton
        aria-label='delete'
        size='large'
        color='inherit'
        style={{ width: 50, height: 50 }}
        href='https://discord.gg/GAGxMVJP'
        target='_blank'
        rel='noreferrer noopener'
      >
        {lightOn
          ? (
            <Discord style={{ width: 20, height: 20, padding: 0, margin: 0 }} />
            )
          : (
            <DiscordWhite
              style={{ width: 20, height: 20, padding: 0, margin: 0 }}
            />
            )}
      </IconButton>
      <IconButton
        aria-label='delete'
        size='large'
        color='inherit'
        href='https://www.reddit.com/user/GradianNetwork'
        target='_blank'
        rel='noreferrer noopener'
      >
        <Reddit />
      </IconButton>
    </Box>
  )
}
