import Box from '@mui/material/Box'

import './style.css'

import { useSelector } from 'react-redux'

export function LogoStrip ({ children }) {
  return <Box className='logostrip'>{children}</Box>
}

export function Logo ({ imgSrc, lightImgSrc }) {
  const lightOn = useSelector((state) => state.light.value)
  if (lightOn) return <img src={imgSrc} height='120px' className='logo' alt='partner' />
  return <img src={lightImgSrc} height='120px' className='logo' alt='partner' />
}
